







































































































    import { Vue } from 'vue-property-decorator';
    import RadioApi, { RadioApiSearchParams } from '@/apiResources/RadioApi';
    import { Radio } from '@/models/Radio';
    import FileUploadApi from '@/apiResources/FileUploadApi';
    import APIS from '@/apisServer';
import { HTTP } from '../../../HTTPServices';

    export default Vue.extend({
        $_veeValidate: {
            validator: 'new'
        },
        components: {},
        data() {
            return {
                valid: false,
                isShow: false,
                isUpdate: false,
                radio: {} as Radio,
                loading: false,
                loadingSave: false,
                searchParamsRadio: {} as RadioApiSearchParams,
                active: [] as any[],
                radioID: 0,
                APIS: APIS,
                files: null as any,
                editorConfig: {
                    height: 300,
                    language: 'vi',
                    toolbarGroups: [
                        { name: 'document', groups: ['mode', 'document', 'doctools'] },
                        { name: 'clipboard', groups: ['clipboard', 'undo'] },
                        { name: 'forms' },
                        { name: 'basicstyles', groups: ['basicstyles'] },
                        { name: 'colors' },
                        { name: 'tools' },
                        { name: 'others' },
                        { name: 'paragraph', groups: ['list', 'indent', 'blocks', 'align', 'bidi'] },
                        { name: 'links' },
                        { name: 'insert' },
                        { name: 'styles' },
                        { name: 'about' },

                    ]
                }
            }
        },
        watch: {
        },
        created() {
        },
        methods: {
            hide() {
                this.isShow = false
            },
            show(isUpdate: boolean, item: any) {
                this.$validator.errors.clear();
                this.$validator.reset();
                this.loadingSave = false;
                this.isShow = true;
                this.files = null as any;
                this.isUpdate = isUpdate;
                if (isUpdate) {
                    this.radioID = item.RadioID;
                    this.getDataFromApi(this.radioID);
                }
                else {
                    this.radio = {} as Radio;
                }
            },
            getDataFromApi(id: number): void {
                RadioApi.get(id).then(res => {
                    this.radio = res;
                });
            },
            addFile(link: any): void {
                var formData = new FormData()
                let files = link
                formData.append('file', files);
                FileUploadApi.upload(formData).then(res => {
                    this.radio.LinkFile = res
                }).catch(res => {
                    this.$snotify.error('Upload file thất bại!');
                })
            },
            changePhoto(): void {
                var formData = new FormData()
                let files = (document as any).querySelector('#File').files[0]
                var re = /^\w+\.(?:jpg|png|gif|jpeg)$/g
                formData.append('img', files);
                FileUploadApi.upload(formData).then(res => {
                    this.radio.AnhDaiDien = res;
                }).catch(res => {
                    this.$snotify.error('Upload ảnh thất bại!');
                })
            },
            
            save(): void {
                this.$validator.validateAll('frmAddEdit').then((res) => {
                    if (res) {
                        if (this.isUpdate) {
                            this.loading = true;
                            RadioApi.update(this.radioID, this.radio).then(res => {
                                this.loading = false;
                                this.$emit("save");
                                this.isShow = false;
                                this.$snotify.success('Cập nhật thành công!');
                            }).catch(res => {
                                this.loading = false;
                                this.$snotify.error('Cập nhật thất bại!');
                            });
                        } else {
                            this.loading = true;
                            RadioApi.insert(this.radio).then(res => {
                                this.radio = res;
                                this.isShow = false;
                                this.$emit("save");
                                this.loading = false;
                                this.$snotify.success('Thêm mới thành công!');
                            }).catch(res => {
                                this.loading = false;
                                this.$snotify.error('Thêm mới thất bại!');
                            });
                        }
                    }
                });
            },
        }
    });
