








































































import { Vue } from 'vue-property-decorator';
import LienHeApi, { LienHeApiSearchParams } from '@/apiResources/LienHeApi';
import { LienHe } from '@/models/LienHe';
import APIS from '@/apisServer';

export default Vue.extend({
    $_veeValidate: {
        validator: 'new'
    },
    components: {},
    data() {
        return {
            valid: false,
            isShow: false,
            isUpdate: false,
            lienHe: {} as LienHe,
            loading: false,
            loadingSave: false,
            searchParamsLienHe: {} as LienHeApiSearchParams,
            active: [] as any[],
            lienHeID: 0,
            APIS: APIS
        }
    },
    watch: {
    },
    created() {
    },
    methods: {
        hide() {
            this.isShow = false
        },
        show(isUpdate: boolean, item: any) {
            this.$validator.errors.clear();
            this.$validator.reset();
            this.loadingSave = false;
            this.isShow = true;
            this.isUpdate = isUpdate;
            if (isUpdate) {
                this.lienHeID = item.LienHeID;
                this.getDataFromApi(this.lienHeID);
            }
            else {
                this.lienHe = {} as LienHe;
            }
        },
        getDataFromApi(id: number): void {
            LienHeApi.get(id).then(res => {
                this.lienHe = res;
            });
        },
        save(): void {
            this.$validator.validateAll('frmAddEdit').then((res) => {
                if (res) {
                    if (this.isUpdate) {
                        this.loading = true;
                        LienHeApi.update(this.lienHeID, this.lienHe).then(res => {
                            this.loading = false;
                            this.$emit("save");
                            this.isShow = false;
                            this.$snotify.success('Cập nhật thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Cập nhật thất bại!');
                        });
                    } else {
                        this.loading = true;
                        LienHeApi.insert(this.lienHe).then(res => {
                            this.lienHe = res;
                            this.isShow = false;
                            this.$emit("save");
                            this.loading = false;
                            this.$snotify.success('Thêm mới thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Thêm mới thất bại!');
                        });
                    }
                }
            });
        },
    }
});
