





































import { Vue } from 'vue-property-decorator';
import LoaiNhanVienApi, { LoaiNhanVienApiSearchParams } from '@/apiResources/LoaiNhanVienApi';
import { LoaiNhanVien } from '@/models/LoaiNhanVien';
import APIS from '@/apisServer';

export default Vue.extend({
    $_veeValidate: {
        validator: 'new'
    },
    components: {},
    data() {
        return {
            valid: false,
            isShow: false,
            isUpdate: false,
            theLoai: {} as LoaiNhanVien,
            loading: false,
            loadingSave: false,
            searchParamsLoaiNhanVien: {} as LoaiNhanVienApiSearchParams,
            active: [] as any[],
            theLoaiID: 0,
            APIS: APIS
        }
    },
    watch: {
    },
    created() {
    },
    methods: {
        hide() {
            this.isShow = false
        },
        show(isUpdate: boolean, item: any) {
            this.$validator.errors.clear();
            this.$validator.reset();
            this.loadingSave = false;
            this.isShow = true;
            this.isUpdate = isUpdate;
            if (isUpdate) {
                this.theLoaiID = item.LoaiNhanVienID;
                this.getDataFromApi(this.theLoaiID);
            }
            else {
                this.theLoai = {} as LoaiNhanVien;
            }
        },
        getDataFromApi(id: number): void {
            LoaiNhanVienApi.get(id).then(res => {
                this.theLoai = res;
            });
        },
        save(): void {
            this.$validator.validateAll('frmAddEdit').then((res) => {
                if (res) {
                    if (this.isUpdate) {
                        this.loading = true;
                        LoaiNhanVienApi.update(this.theLoaiID, this.theLoai).then(res => {
                            this.loading = false;
                            this.$emit("save");
                            this.isShow = false;
                            this.$snotify.success('Cập nhật thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Cập nhật thất bại!');
                        });
                    } else {
                        this.loading = true;
                        LoaiNhanVienApi.insert(this.theLoai).then(res => {
                            this.theLoai = res;
                            this.isShow = false;
                            this.$emit("save");
                            this.loading = false;
                            this.$snotify.success('Thêm mới thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Thêm mới thất bại!');
                        });
                    }
                }
            });
        },
    }
});
