




















































import { Vue } from 'vue-property-decorator';
import ChuyenMucApi, { ChuyenMucApiSearchParams } from '@/apiResources/ChuyenMucApi';
import { ChuyenMuc } from '@/models/ChuyenMuc';
export default Vue.extend({
    $_veeValidate: {
        validator: 'new'
    },
    components: {},
    data() {
        return {
            isShow: false,
            isUpdate: false,
            chuyenMuc: {} as ChuyenMuc,
            loading: false,
            loadingSave: false,
            searchParamsChuyenMuc: {} as ChuyenMucApiSearchParams,
            active: [] as any[],
            open: [],
            chuyenMucID: 0,
        }
    },
    watch: {
    },
    methods: {
        hide() {
            this.isShow = false
        },
        show(isUpdate: boolean, item: any) {
            this.$validator.reset();
            this.loadingSave = false;
            this.isShow = true;
            this.isUpdate = isUpdate;
            if (isUpdate) {
                this.chuyenMucID = item.ChuyenMucID;
                this.getDataFromApi(this.chuyenMucID);
            }
            else {
                this.chuyenMuc = {} as ChuyenMuc;
            }
        },
        getDataFromApi(id: number): void {
            ChuyenMucApi.get(id).then(res => {
                this.chuyenMuc = res;
            });
        },
        save(): void {
            this.$validator.validateAll('frmAddEdit').then((res) => {
                if (res) {
                    if (this.isUpdate) {
                        this.loading = true;
                        ChuyenMucApi.update(this.chuyenMucID, this.chuyenMuc).then(res => {
                            this.loading = false;
                            this.$emit("save");
                            this.isShow = false;
                            this.$snotify.success('Cập nhật thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Cập nhật thất bại!');
                        });
                    } else {
                        this.loading = true;
                        ChuyenMucApi.insert(this.chuyenMuc).then(res => {
                            this.chuyenMuc = res;
                            this.isShow = false;
                            this.$emit("save");
                            this.loading = false;
                            this.$snotify.success('Thêm mới thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Thêm mới thất bại!');
                        });
                    }
                }
            });
        },
    }
});
